.file-uploader {
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
}

.file-uploader-button {
  padding: 10px;
  background-color: var(--color-primary-dark);
  color: var(--color-primary-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  word-break: break-word;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.file-uploader-button:hover {
  background-color: var(--color-secondary-dark);
  color: var(--color-primary-light);
}

.file-list-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}

.toggle-list-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  margin: 0px;
  display: flex;
  align-items: center;
}

.toggle-list-button span {
  font-size: 16px;
  margin-right: 10px;
}

.file-list {
  list-style: none;
  margin: 0;
  padding: 0px 7px;
  width: 100%;
  min-width: 400px;
  position: absolute;
  bottom: 100%;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  left: 0;
  background-color: var(--color-background);
  max-height: 0;
  overflow: hidden;
  transform: scaleY(0);
  opacity: 0;
  transform-origin: bottom;
  transition: transform 0.3s ease-out, opacity 0.3s ease-out;
  z-index: 1;
}

.file-list.visible {
  max-height: 300px; /* adjust as needed */
  transform: scaleY(1);
  opacity: 1;
  transition: transform 0.3s ease-in, opacity 0.3s ease-in;
  padding: 7px;
}

.file-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  padding: 7px;
  border: 1px solid var(--color-border);
  border-radius: 8px;
  margin-bottom: 5px;
  background-color: var(--color-background);
}

.file-actions {
  display: flex;
  align-items: center;
}

.file-icon {
  margin-right: 10px;
  font-size: 15px;
  cursor: pointer;
}

.file-icon.success {
  color: var(--color-success);
}

.file-icon.error {
  color: var(--color-error);
}

.file-icon.delete:hover {
  color: var(--color-error);
}
.file-icon.delete {
  color: var(--color-secondary-dark);
}
