.statistics-container {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 20px;
}

.statistics-header {
  display: flex;
  justify-content: space-between;
}

.statistics-container h3 {
  font-size: 20px;
  margin-bottom: 15px;
  color: #555;
}

.statistics-container h4 {
  font-size: 18px;
  margin-bottom: 10px;
  color: #666;
}

.filter-container {
  margin-bottom: 20px;
}

.filter-container label {
  display: block;
  margin-bottom: 5px;
  font-size: 16px;
}

.filter-container select,
.filter-container .react-select__control {
  width: 100%;
  max-width: 300px;
  margin-bottom: 15px;
}

.chart-container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.chart {
  flex: 1;
}

.completion-percentage {
  margin-top: 20px;
  text-align: center;
  font-size: 18px;
  color: #333;
}

.product-filters {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  gap: 20px;
}

.product-filters label {
  font-size: 16px;
  cursor: pointer;
}

.product-filters input {
  margin-right: 5px;
}

.toggle-products-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.toggle-products-button:hover {
  background-color: #0056b3;
}

.product-list {
  margin-top: 20px;
  max-height: 300px;
  overflow-y: auto;
  transition: max-height 0.5s ease-out;
}

.product-list ul {
  list-style-type: none;
  padding: 0;
}

.product-list li {
  background-color: #f1f1f1;
  padding: 10px;
  border: 1px solid #ddd;
  margin-bottom: 5px;
  border-radius: 5px;
}

.product-list li a {
  color: #007bff;
  text-decoration: none;
}

.product-list li a:hover {
  text-decoration: underline;
}

.close-button {
  display: block;
  padding: 10px 20px;
  margin: 20px auto 0;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.close-button:hover {
  background-color: #0056b3;
}
