.products-table-container {
  width: 100%;
  margin: 0;
  background-color: var(--color-background);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow-x: auto; /* Добавлено для горизонтальной прокрутки */
}

.table-controls {
  display: flex;
  flex-wrap: wrap; /* Добавлено для адаптивности */
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.products-table {
  width: 100%;
  border-collapse: collapse;
  color: var(--color-text-dark);
  font-size: 14px;
  table-layout: auto; /* Добавлено для автоматического управления шириной */
}

.products-table th,
.products-table td {
  border: 1px solid #f3f2f2;
  padding: 4px;
  text-align: left;
}

.products-table th {
  background-color: var(--color-background);
  border-bottom: 2px solid var(--color-border);
  font-size: 14px;
  color: var(--color-secondary-dark);
}

.products-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.products-table tr:nth-child(odd) {
  background-color: var(--color-background);
}

.products-table tr:hover {
  background-color: var(--color-input-bg);
}

.column-filter-input {
  font-size: 12px;
  max-width: 300px;
}

.multi-select-filter {
  font-size: 11px;
  width: 100%;
  padding: 2px;
  border: none;
  border-radius: 5px;
}

.pagination {
  display: flex;
  flex-wrap: wrap; /* Добавлено для адаптивности */
  align-items: center;
  justify-content: center;
  color: var(--color-primary-dark);
}

.pagination button {
  margin: 5px 5px; /* Обновлено для лучшей адаптивности */
  padding: 8px;
  cursor: pointer;
  background-color: var(--color-secondary-light);
  border: none;
  border-radius: 5px;
  color: var(--color-secondary-dark);
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.pagination button:hover {
  background-color: var(--color-primary-light);
  border: none;
}

.pagination button:disabled {
  background-color: #eee;
  cursor: not-allowed;
}

.pagination input {
  margin: 5px 5px; /* Обновлено для лучшей адаптивности */
  width: 40px;
  text-align: center;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 5px;
  color: #333;
  padding: 8px;
  font-size: 12px;
}

.pagination select {
  margin: 5px 5px; /* Обновлено для лучшей адаптивности */
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 5px;
  color: #333;
  padding: 8px;
  font-size: 12px;
}

.input-products-search {
  font-size: 14px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.loading {
  text-align: center;
  padding: 50px;
  font-size: 18px;
}
